<template>
    <div class="product-card">
      <!-- 图片区域 -->
      <div class="image-container">
        <img :src="product.image" :alt="product.title" class="product-image">
        
      </div>
  
      <!-- 商品信息 -->
      <div class="product-info">
        <div class="product-title">{{ product.title }}</div>
        
        <!-- 商品标签 -->
        <div class="tags-container">
            <span v-if="product.source" class="tag red-filled">{{ platformMap[product.source] || product.source }}</span>
            <span v-if="product.isby" class="tag yellow-outline">包邮</span>
          <span 
            v-for="(tag, index) in product.tags" 
            :key="index"
            class="tag red-outline"
          >
            {{ tag }}
          </span>
        </div>
  
        <!-- 价格和付款人数 -->
        <div class="price-section">
          <span class="current-price"><span style="font-size: 12px;">¥</span>{{ product.price }}</span>
          <span class="sold-count">{{ product.sold }}人付款</span>
        </div>
      </div>
    </div>
  </template>
  
  <script>
  export default {
    name: 'ProductCard',
    props: {
      product: {
        type: Object,
        required: true,
        default: () => ({
          image: '',
          title: '',
          source: '',  // 来源标识
          isby: false, 
          tags: [],
          price: 0,
          sold: 0
        })
      }
    },
  
    data() {
      return {
        // 平台映射
        platformMap: {
          tmall: '天猫',
          jd: '京东',
          pdd: '拼多多'
        }
      }
    }
  }
  </script>
  
  <style scoped>
  .product-card {
    width: 46%;
    border-radius: 8px;
    overflow: hidden;
    box-shadow: 0 2px 8px rgba(0,0,0,0.1);
    transition: transform 0.2s;
    background: white;
    margin: 5px;
  }
  
  .product-card:hover {
    transform: translateY(-4px);
  }
  
  .image-container {
    position: relative;
    width: 100%;
    padding-top: 100%; /* 保持1:1宽高比 */
  }
  
  .product-image {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

/* 基础标签样式 */
.tag {
  display: inline-block;
  padding: 0px 2px;
  border-radius: 2px;
  font-size: 12px;
  font-weight: 400;
  margin: 1px;
}

/* 白边框红底白字 */
.red-filled {
  border: 1.5px solid #ff4444;  
  background-color: #ff4444;  
  color: #ffffff;             
  
}

/* rgb(207, 93, 44)边框白底红字 */
.red-outline {
  border: 1.5px solid rgb(207, 93, 44);  
  background-color: #ffffff;  
  color: rgb(207, 93, 44);           
}
.yellow-outline {
  border: 1.5px solid rgb(221, 170, 54);  
  background-color: #ffffff;  
  color: rgb(221, 170, 54);           
}

  .product-info {
    padding: 8px;
  }
  
  .product-title {
  /* 单行文字溢出隐藏 */
    white-space: nowrap;          /* 禁止换行 */
    overflow: hidden;             /* 隐藏溢出内容 */
    text-overflow: ellipsis;      /* 显示省略号 */
    max-width: 200px;             /* 根据实际容器宽度调整 */

    /* 可选样式调整 */
    font-size: 14px;             /* 字体大小 */
    color: #333;                 /* 文字颜色 */
    margin: 0;                   /* 移除默认外边距 */
    
    /* 溢出无省略号，直接裁剪 */
    /* white-space: nowrap;      
    overflow: hidden;         
    max-width: 200px;         
    text-overflow: clip;       
    font-family: sans-serif;    */

  }
  
  .tags-container {
    margin: 6px 0;
    display: flex;
    flex-wrap: wrap;
    gap: 1px;
  }
  
  .product-tag {
    font-size: 12px;
    padding: 2px 6px;
    border-radius: 2px;
    white-space: nowrap;
  }
  
  .price-section {
    display: flex;
    align-items: baseline;
    justify-content: space-between;
    gap: 8px;
  }
  
  .current-price {
    color: #ff0036;
    font-size: 20px;
    font-weight: 700;
  }
  
  .sold-count {
    color: #999;
    font-size: 12px;
    
  }
  </style>