<template>
  <div class="carousel-container">
    <!-- 轮播内容 -->
    <div class="carousel-track" :style="trackStyle">
      <div 
        v-for="(slide, index) in slides"
        :key="index"
        class="carousel-item"
        :class="{ active: currentIndex === index }"
      >
        <img :src="slide.image" class="carousel-image">
      </div>
    </div>

    <!-- 导航点 -->
    <div class="carousel-dots">
      <span
        v-for="(dot, index) in slides"
        :key="index"
        :class="{ active: currentIndex === index }"
        @click="goTo(index)"
      ></span>
    </div>

    <!-- 导航箭头 -->
    <button class="carousel-prev" @click="prev">‹</button>
    <button class="carousel-next" @click="next">›</button>
  </div>
</template>

<script>
export default {
    name: 'BannerViews',
    props: {
      slides: {
        type: Array,
        required: true
      }
    },
  data() {
    return {
      currentIndex: 0,
      
      autoPlayInterval: null
    }
  },
  computed: {
    trackStyle() {
      return {
        transform: `translateX(-${this.currentIndex * 100}%)`
      }
    }
  },
  methods: {
    next() {
      this.currentIndex = (this.currentIndex + 1) % this.slides.length
    },
    prev() {
      this.currentIndex = (this.currentIndex - 1 + this.slides.length) % this.slides.length
    },
    goTo(index) {
      this.currentIndex = index
    }
  },
  mounted() {
    // 自动播放
    this.autoPlayInterval = setInterval(() => {
      this.next()
    }, 3000)
  },
  beforeDestroy() {
    clearInterval(this.autoPlayInterval)
  }
}
</script>

<style scoped>
.carousel-container {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.carousel-track {
  display: flex;
  height: 100%;
  transition: transform 0.5s ease;
}

.carousel-item {
  flex: 0 0 100%;
  height: 100%;
  opacity: 0;
  transition: opacity 0.5s ease;
}

.carousel-item.active {
  opacity: 1;
}

.carousel-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.carousel-dots {
  position: absolute;
  bottom: 10px;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  gap: 10px;
}

.carousel-dots span {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background: rgba(255,255,255,0.5);
  cursor: pointer;
  transition: background 0.3s ease;
}

.carousel-dots span.active {
  background: #fff;
}

.carousel-prev,
.carousel-next {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background: rgba(0,0,0,0.5);
  color: white;
  border: none;
  padding: 15px 20px;
  cursor: pointer;
  font-size: 24px;
}

.carousel-prev {
  left: 10px;
  background: transparent;
}

.carousel-next {
  right: 10px;
  background: transparent;
}
</style>