<template>
  <div class="classifyBoxContent">
    <div class="box" v-if="!isMore">
      <img class="pic" :src="item.picurl" alt="" />
      <div class="mask">{{ item.name }}</div>
      <div class="tag">{{ timeAgo(item.updateTime) }}天前更新</div>
    </div>

    <div class="box" v-if="isMore">
      <img class="pic" src="../assets/classify.png" alt="" />
      <div class="mask more">
        <div class="dot">···</div>
        <div class="text">更多</div>
      </div>
    </div>
  </div>
</template>
  
  <script>
export default {
  name: "ClassifyTheme",
  props: {
    isMore: {
      type: Boolean,
      default: false,
    },
    item: {
      type: Object,
      default: () => {},
    },
  },
  data() {
    return {
      classifyList: [
        {
          id: 1,
          update: 30,
          picurl: require("@/assets/doge.png"),
          title: "doge",
        },
      ],
    };
  },
  methods: {
    timeAgo(timestamp) {
      const now = Date.now(); // 当前时间的时间戳
      const diff = Math.floor((now - timestamp) / 1000); // 计算时间差（秒）
      const minutes = Math.floor(diff / 60); // 转换为分钟
      const hours = Math.floor(minutes / 60); // 转换为小时
      const days = Math.floor(hours / 24); // 转换为天
      const months = Math.floor(days / 30); // 转换为月（这里假设一个月平均30天）

      if (diff < 60) {
        return "刚刚更新"; // 你可以根据需要修改这个返回值
      } else if (minutes < 60) {
        return `${minutes}分钟前更新`;
      } else if (hours < 24) {
        return `${hours}小时前更新`;
      } else if (days < 30) {
        return `${days}天前更新`;
      } else if (months < 3) {
        return `${months}个月前更新`;
      } else {
        return null;
      }
    },
  },
};
</script>
  
<style scoped>
.classifyBoxContent {
  width: 32%;
  aspect-ratio: 53/81;
  margin: 2px;
  overflow: hidden;
  transition: transform 0.2s;
}
.classifyBoxContent:hover {
    transform: translateY(-4px);
  }
.box {
  height: 100%;
  width: 100%;
  border-radius: 5px;
  overflow: hidden;
  position: relative;
}
.pic {
  width: 100%;
  height: 100%;
}
.mask {
  width: 100%;
  height: 35px;
  position: absolute;
  bottom: 0;
  left: 0;
  background: rgba(0, 0, 0, 0.2);
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  backdrop-filter: blur(10px);
  font-weight: 600;
  font-size: 15px;
}
.tab {
  position: absolute;
  left: 0;
  top: 0;
  background: rgba(250, 129, 90, 0.7);
  backdrop-filter: 10px;
  color: #fff;
  font-size: 11px;
  padding: 3px 7px;
  border-radius: 0 0 20rpx 0;
  transform: scale(0.8);
  transform-origin: left top;
}
.more {
  width: 100%;
  height: 100%;
  flex-direction: column;
}
.text {
  font-size: 19px;
}
</style>