                    </div>
                    <div class="date">
                      <img
                        src="@/assets/icon/date.png"
                        alt=""
                        style="width: 20px; height: 20px; margin-right: 5px"
                      />
                      <span
                        class="dateText"
                        style="font-size: 20px; color: #28b389"
                        >{{
                          String(new Date(Date.now()).getDate()).padStart(
                            2,
                            "0"
                          )
                        }}</span
                      >
                    </div>
                  </div>
                  <!-- 每日推荐图片 -->
                  <div class="picScroll_everyday">
                    <div
                      class="picToday"
                      v-for="(item, index) in todayImgs"
                      :key="index"
                    >
                      <img :src="item.image" alt="" />
                    </div>
                  </div>
                  <!-- 专题精选 -->
                  <div class="everyday_recommend">
                    <div style="font-size: 20px; font-weight: 600">
                      专题精选
                    </div>
                    <div
                      style="font-size: 20px; font-weight: 500; color: #a7a7a7"
                    >
                      More+
                    </div>
                  </div>

                  <div class="classify_content">
                    <ClassifyTheme
                      v-for="item in classifyList"
                      :key="item._id"
