                </div>
                <div class="buttomtab">
                  <img src="../../assets/icon/tabbar/classify.png" alt="" />
                  <span style="color: #a7a7a7">分类</span>
                </div>
                <div class="buttomtab">
                  <img src="../../assets/icon/tabbar/user.png" alt="" />
                  <span style="color: #a7a7a7">我的</span>
                </div>
              </div>
            </div>

            <!-- 右边展示页 -->
            <div class="rightContent phone_page">
              <div class="ProView">
                <img
                  src="../../assets/dogProview.jpg"
                  alt=""
                  style="width: 100%; height: 100%"
                />
                <capsule-dot class="capsule-dot-topright"></capsule-dot>
                <div class="goBackBtn">
                  <img src="../../assets/icon/left.png" alt="" />
                </div>
                <div class="pageIndex">1 / 13</div>
                <div class="clock">
                  <!-- 时间显示 -->
                  <div class="time">
                    <span class="hours">{{ hours }}</span>
                    <span class="colon">:</span>
                    <span class="minutes">{{ minutes }}</span>
                  </div>
                  <!-- 日期显示 -->
                  <div class="date">
                    {{ formattedDate }}
                  </div>
                </div>
                <div class="MsgBox">
                  <button class="msg-btn" @click="toggleMenu">
                    <i class="el-icon-warning-outline"></i>
                    <span>信息</span>
                  </button>

                  <button class="msg-btn" @click="toggle">
                    <i class="el-icon-star-off"></i>
                    <span>评分</span>
                  </button>

                  <button class="msg-btn">
                    <i class="el-icon-download"></i>
                    <span>下载</span>
                  </button>
                </div>
                <!-- 中间评分框 -->
                <transition name="scale">
                  <div v-show="showModal" class="modal">
                    <div class="cancel" @click="toggle">x</div>
                    <h1>暂无评分</h1>
                  </div>
                </transition>

                <!-- 底部菜单 -->
                <transition name="slide-up">
                  <div v-show="showMenu" class="menu">
                    <div class="cancel" @click="toggleMenu">x</div>
                    <h1>暂无信息</h1>
                  </div>
                </transition>
              </div>
            </div>
          </div>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>
    
    <script>
import AsideMenu from "../../components/AsideMenu.vue";
import ProductCard from "../../components/ProductCard.vue";
import AdProduct from "../../components/AdProduct.vue";
import PageWrapper from "../../components/PageWrapper.vue";
import BannerViews from "../../components/BannerViews.vue";
import CapsuleDot from "../../components/CapsuleDot.vue";
import ClassifyTheme from "@/components/ClassifyTheme.vue";

export default {
  name: "kbsview",
  components: {
    AsideMenu,
    ProductCard,
    AdProduct,
    PageWrapper,
    BannerViews,
    CapsuleDot,
    ClassifyTheme,
  },
  data() {
    return {
      now: new Date(),
      showModal: false, //弹出窗口
      showMenu: false, //菜单从下弹出
      isCollapse: false, //默认不收缩
      asideWidth: "200px",
      collapseIcon: "el-icon-s-fold",
      slides: [
        {
          image:
            "https://mp-0cb878b7-99ec-44ea-8246-12b123304b05.cdn.bspapp.com/xxmBizhi/banner/1699255294420_3921.jpg",
        },
        {
          image:
            "https://mp-0cb878b7-99ec-44ea-8246-12b123304b05.cdn.bspapp.com/xxmBizhi/banner/1699256636265_3184.jpg",
        },
        {
          image:
            "https://mp-0cb878b7-99ec-44ea-8246-12b123304b05.cdn.bspapp.com/xxmBizhi/banner/1699255179817_5858.jpg",
        },
      ],
      texts: [
        // 轮播文本数组
        "云桑开发-uniapp小程序开发详情",
        "开发者-李东云-联系邮箱：******",
      ],
      currentIndex: 0, // 当前显示的文本索引
      offset: 0, // 控制动画偏移量（0~100）
      isAnimating: false, // 防止动画冲突
      interval: null, // 定时器
      duration: 3000, // 轮播间隔（3秒）
      todayImgs: [
        {
          image:
            "https://tse4-mm.cn.bing.net/th/id/OIP-C.8JqHT1NqCG2xpVj8t3FtKwHaQA?w=161&h=350&c=7&r=0&o=5&dpr=1.8&pid=1.7",
        },
        {
          image:
            "https://tse3-mm.cn.bing.net/th/id/OIP-C.IkzDIfrk_2R_i0UhkSDhOAHaNK?w=186&h=331&c=7&r=0&o=5&dpr=1.8&pid=1.7",
        },
        {
          image:
            "https://tse2-mm.cn.bing.net/th/id/OIP-C.Q-4bcttDqyjg2RWHzhkO3AHaNK?w=186&h=331&c=7&r=0&o=5&dpr=1.8&pid=1.7",
        },
        {
          image:
            "https://tse1-mm.cn.bing.net/th/id/OIP-C.K66j0A5UtjH83pSom4h-AAHaQC?w=186&h=368&c=7&r=0&o=5&dpr=1.8&pid=1.7",
        },
        {
          image:
            "https://tse3-mm.cn.bing.net/th/id/OIP-C._3YQt6JogXPAJTFvFmXADwHaQd?w=157&h=349&c=7&r=0&o=5&dpr=1.8&pid=1.7",
        },
        {
          image:
            "https://tse4-mm.cn.bing.net/th/id/OIP-C.Ne9l4_q2m7cjJ6qjcMr-IgHaQB?w=161&h=350&c=7&r=0&o=5&dpr=1.8&pid=1.7",
        },
      ],
      classifyList: [
        {
          name: "可爱萌宠",
          sort: 1,
          picurl:
            "https://mp-0cb878b7-99ec-44ea-8246-12b123304b05.cdn.bspapp.com/xxmBizhi/20231010/1696900747351_2102.jpg",
          select: true,
          enable: true,
          updateTime: 1743940716252,
          _id: "6524a48f6523417a8a8b825d",
        },
        {
          name: "AI美图",
          sort: 2,
          picurl:
            "https://mp-0cb878b7-99ec-44ea-8246-12b123304b05.cdn.bspapp.com/xxmBizhi/20231010/1696902369329_5036.jpg",
          select: true,
          enable: true,
          updateTime: 1743939918102,
          _id: "6524ace7213929cbcee72e4d",
        },
        {
          name: "平铺卡通",
          sort: 2,
          picurl:
            "https://mp-0cb878b7-99ec-44ea-8246-12b123304b05.cdn.bspapp.com/xxmBizhi/20231010/1696901099086_3215.jpg",
          select: false,
          enable: true,
          updateTime: 1742955830929,
          _id: "65237596189f860b7614d971",
        },
        {
          name: "自然风光",
          sort: 4,
          picurl:
            "https://mp-0cb878b7-99ec-44ea-8246-12b123304b05.cdn.bspapp.com/xxmBizhi/20231010/1696901491940_1839.jpg",
          select: true,
          enable: true,
          updateTime: 1742264887598,
          _id: "6523757d466d417a37a40d75",
        },
        {
          name: "明星美女",
          sort: 5,
          picurl:
            "https://cdn.qingnian8.com/public/bizhiClassify/1698737067256_7406.jpg",
          select: true,
          enable: true,
          updateTime: 1745755754706,
          _id: "65237031189f860b7613acf4",
        },
        {
          name: "创意色彩",
          sort: 5,
          picurl:
            "https://mp-0cb878b7-99ec-44ea-8246-12b123304b05.cdn.bspapp.com/xxmBizhi/20231010/1696902573130_5976.jpg",
          select: true,
          enable: true,
          updateTime: 1740722038724,
          _id: "6524adaffe975f09c72ce896",
        },
        {
          name: "帅哥靓仔",
          sort: 6,
          picurl:
            "https://mp-0cb878b7-99ec-44ea-8246-12b123304b05.cdn.bspapp.com/xxmBizhi/20231010/1696903729761_0302.jpg",
          select: false,
          enable: true,
          updateTime: 1743951115973,
          _id: "6524af7ae0ec19c8d601a96d",
        },
        {
          select: true,
          name: "独特建筑",
          sort: 7,
          picurl:
            "https://cdn.qingnian8.com/public/bizhiClassify/1698726300325_8360.jpg",
          enable: true,
          updateTime: 1743488117564,
          _id: "652b59eb8b0da4ca08b7453e",
        },
      ],
    };
  },
  computed: {
    // 当前显示的文字
    currentText() {
      return this.texts[this.currentIndex];
    },
    // 下一个要显示的文字
    nextText() {
      return this.texts[(this.currentIndex + 1) % this.texts.length];
    },
    // 时间计算
    hours() {
      return this.now.getHours().toString().padStart(2, "0");
    },
    minutes() {
      return this.now.getMinutes().toString().padStart(2, "0");
    },

    // 日期格式化
    formattedDate() {
      const options = {
        weekday: "long",
        month: "long",
        day: "numeric",
      };
      return this.now.toLocaleDateString("zh-CN", options);
    },
  },
  mounted() {
    this.startAutoPlay();
    // 启动定时器
    this.timer = setInterval(() => {
      this.now = new Date();
    }, 1000);
  },
  beforeDestroy() {
    this.stopAutoPlay();
    // 清除定时器
    clearInterval(this.timer);
  },
  methods: {
    handleFull() {
      // 全屏
      document.documentElement.requestFullscreen();
    },
    handleCollapse() {
      this.isCollapse = !this.isCollapse;
      this.asideWidth = this.isCollapse ? "64px" : "200px";
      this.collapseIcon = this.isCollapse
        ? "el-icon-s-unfold"
        : "el-icon-s-fold";
    },
    rulete() {
      this.$router.push("/login").catch((err) => {
        console.error("路由跳转失败:", err);
      });
    },
    //公告部分
    // 开始自动播放
    startAutoPlay() {
      this.interval = setInterval(() => {
        if (!this.isAnimating) {
          this.playAnimation();
        }
      }, this.duration);
    },
    // 停止自动播放
    stopAutoPlay() {
      clearInterval(this.interval);
    },
    // 执行动画
    playAnimation() {
      this.isAnimating = true;
      let start = null;
      const animate = (timestamp) => {
        if (!start) start = timestamp;
        const progress = timestamp - start;
        // 计算动画进度（0~1）
        this.offset = (progress / 100) * 100; // 500ms完成动画

        if (progress < 500) {
          requestAnimationFrame(animate);
        } else {
          // 动画完成后切换到下一索引
          this.currentIndex = (this.currentIndex + 1) % this.texts.length;
          this.offset = 0;
          this.isAnimating = false;
        }
      };
      requestAnimationFrame(animate);
    },
    toggle() {
      this.showModal = !this.showModal;
    },
    toggleMenu() {
      this.showMenu = !this.showMenu;
    },
    toggleDownload(){
      alert("当前仅为预览，请前往小程序下载！")
    },
  },
};
</script>
    
    <style scoped>
