  max-width: 920px; /* 控制整体居中宽度（按需调整） */
  margin: 0 auto; /* 水平居中 */
  display: flex;
  justify-content: center; /* 关键：让子项向中间靠拢 */
  gap: 20px; /* 两个盒子之间的间距 */
  align-items: flex-start; /* 顶部对齐 */
}
.leftContent,
.rightContent {
  width: 450px; /* 固定宽度（按需修改） */
  /* padding: 5px; */
  border: 1px solid #ddd;
  background: #f8f8f8;
  /* 确保内容增长方向正确 */
  display: flex;
  flex-direction: column;
  background: linear-gradient(to bottom, transparent, #fff 200px),
    linear-gradient(to right, rgba(121, 193, 211), #ffe8e9, pink);
}
.phone_page {
  height: 88vh;
  width: 44vh;
  /* padding: 2vh 0; */
  border: 4px solid rgb(24, 24, 24);
  border-radius: 30px;
  overflow: hidden;
}
