  width: 100%;
  /* border: 1px solid #3855ae; */
  padding: 10px 8px 5px 8px;
  margin-bottom: 10px;
}
.title {
  font-size: 22px;
  font-weight: 700;
  color: #000;
}
.search {
  width: 30%;
  aspect-ratio: 217/47;
  border-radius: 10px;
  background: rgba(255, 255, 255, 0.4);
  border: 1px solid #fff;
  margin-right: 30px;
  color: #999;
  font-size: 14px;
  display: flex;
  align-items: center;
  justify-content: start;
}
.el-icon-search {
  /* width: 20px;
    height: 20px; */
  margin-left: 5px;
}
.search_text {
  padding-left: 5px;
}

/* 公告 */
.notice {
  display: flex;
  align-items: center;
  padding: 0 5px;
  margin: 10px 5px;
  background: rgba(246, 246, 246, 0.7);
  /* border: 1px solid #444853; */
  border-radius: 20px;
}
.noticeLeft {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 5px;
  /* border: 1px solid #7b93dc; */
}
.noticeText {
  color: #28b389;
  font-size: 14px;
  font-weight: 600;
  margin: 0 2px;
}
.noticeRight {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 10px;
  /* border: 1px solid #7b93dc; */
}
/* 公告内容 */
.noticeCenter {
  width: 70%;
  /* border: 1px solid #7b93dc; */
}
.text-carousel {
  width: 100%;
  height: 20px; /* 根据文字高度调整 */
  overflow: hidden;
  position: relative;
  margin: 10px auto;
  /* border: 1px solid #eee; */
  border-radius: 4px;
}

.carousel-container {
  position: relative;
  height: 100%;
}

.text-item {
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
